import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Typography, Card, CardContent, Container, CssBaseline, Box } from '@mui/material';
import { ref, onValue } from 'firebase/database';
import { db } from './firebasec';

function ClassDetailsPage() {
    const { id } = useParams();
    const [students, setStudents] = useState([]);

    useEffect(() => {
        const classRef = ref(db, `classes/${id}/students`);
        onValue(classRef, (snapshot) => {
            const data = snapshot.val();
            if (data) {
                const parsedStudents = Object.keys(data).map(key => ({
                    id: key,
                    ...data[key]
                }));
                setStudents(parsedStudents);
            } else {
                setStudents([]);
            }
        });
    }, [id]);

    return (
        <Container component="main" maxWidth="md">
            <CssBaseline />
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Typography component="h1" variant="h5">
                    Class Details
                </Typography>
                <Card sx={{ mt: 3, p: 3, width: '100%' }}>
                    <CardContent>
                        <Typography variant="h6">Students</Typography>
                        {students.map(({ id, studentName }) => (
                            <Typography key={id} variant="body2">{studentName}</Typography>
                        ))}
                    </CardContent>
                </Card>
            </Box>
        </Container>
    );
}

export default ClassDetailsPage;
