import React, { useState } from 'react';
import { Outlet, Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, IconButton, Drawer, List, ListItem, ListItemText, Box, CssBaseline } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

function Layout() {
    const [open, setOpen] = useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <Box sx={{ display: 'flex' }} >
            <CssBaseline />
            <AppBar
                position="fixed"
                sx={{
                    width: '100%',
                    zIndex: (theme) => theme.zIndex.drawer + 1,
                    bgcolor: '#9c27b0'
                }}
                
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        sx={{ marginRight: 2 }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap>
                        AnswerHub
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer
                variant="persistent"
                anchor="left"
                open={open}
                sx={{
                    width: 240,
                    flexShrink: 0,
                    '& .MuiDrawer-paper': {
                        width: 240,
                        boxSizing: 'border-box',
                    },
                }}
            >
                <Toolbar />
                <List>
                    <ListItem button component={Link} to="/home" onClick={handleDrawerClose}>
                        <ListItemText primary="Home" />
                    </ListItem>
                    <ListItem button component={Link} to="calendar" onClick={handleDrawerClose}>
                        <ListItemText primary="Calendar" />
                    </ListItem>
                    <ListItem button component={Link} to="teacher" onClick={handleDrawerClose}>
                        <ListItemText primary="Teacher" />
                    </ListItem>
                    <ListItem button component={Link} to="student" onClick={handleDrawerClose}>
                        <ListItemText primary="Student" />
                    </ListItem>
                </List>
            </Drawer>
            <Box
                component="main"
                sx={{
                    flexGrow: 1,
                    p: 3,
                    width: `calc(100% - 240px)`,
                    transition: 'margin 0.3s ease',
                    ml: open ? 240 : 0,
                }}
            >
                <Toolbar />
                <Outlet />
            </Box>
        </Box>
    );
}

export default Layout;
