import React, { useState, useEffect } from 'react';
import { Typography, Card,CardActions, CardContent, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField, Grid, Box, Container, CssBaseline, Avatar, Snackbar, Alert } from '@mui/material';
import { ref, push, onValue, query, orderByChild, equalTo } from 'firebase/database';
import ClassIcon from '@mui/icons-material/Class';
import { db } from './firebasec';

function Student() {
    const [open, setOpen] = useState(false);
    const [classCode, setClassCode] = useState('');
    const [studentName, setStudentName] = useState('');
    const [joinedClasses, setJoinedClasses] = useState([]);
    const [email, setEmail] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState('');
    const [snackbarSeverity, setSnackbarSeverity] = useState('success');

    useEffect(() => {
        const userEmail = localStorage.getItem('userEmail');
        if (userEmail) {
            setEmail(userEmail);
            fetchJoinedClasses(userEmail);
        } else {
            // Redirect to login page if no userEmail in local storage
            window.location.href = '/login';
        }
    }, []);

    const fetchJoinedClasses = (userEmail) => {
        console.log("Fetching joined classes for", userEmail);
        const joinedClassesRef = query(ref(db, 'joinclass'), orderByChild('email'), equalTo(userEmail));
        onValue(joinedClassesRef, (snapshot) => {
            const data = snapshot.val();
            console.log("Fetched data:", data);
            if (data) {
                const parsedJoinedClasses = Object.keys(data).map(key => ({
                    id: key,
                    ...data[key]
                }));
                setJoinedClasses(parsedJoinedClasses);
            } else {
                setJoinedClasses([]);
            }
        });
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleJoin = async (e) => {
        e.preventDefault();

        if (!classCode || !studentName) {
            setSnackbarMessage('Class code and name are required');
            setSnackbarSeverity('error');
            setSnackbarOpen(true);
            return;
        }

        try {
            const classRef = query(ref(db, 'classes'), orderByChild('code'), equalTo(classCode));
            onValue(classRef, async (snapshot) => {
                const data = snapshot.val();
                if (data) {
                    const classData = Object.values(data)[0];
                    const joinClassRef = ref(db, 'joinclass');
                    await push(joinClassRef, {
                        className: classData.className,
                        subject: classData.subject,
                        classCode,
                        studentName,
                        email
                    });
                    setClassCode('');
                    setStudentName('');
                    setOpen(false);
                    setSnackbarMessage('Successfully joined the class');
                    setSnackbarSeverity('success');
                    fetchJoinedClasses(email);
                } else {
                    setSnackbarMessage('Invalid class code');
                    setSnackbarSeverity('error');
                }
                setSnackbarOpen(true);
            }, { onlyOnce: true });
        } catch (error) {
            console.error("Error joining class:", error.message);
            setSnackbarMessage(`An error occurred while joining the class: ${error.message}`);
            setSnackbarSeverity('error');
            setSnackbarOpen(true);
        }
    };

    const handleSnackbarClose = () => {
        setSnackbarOpen(false);
    };

    return (
        <Container component="main" maxWidth="md">
            <CssBaseline />
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <ClassIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Student Page
                </Typography>
                <Card sx={{ mt: 3, p: 3, width: '100%' }}>
                    <CardContent>
                        <Typography variant="h6">Join a Class</Typography>
                    </CardContent>
                    <CardActions>
                        <Button variant="contained" color="primary" onClick={handleClickOpen}>Join Class</Button>
                    </CardActions>
                </Card>

                <Grid container spacing={2} sx={{ mt: 3 }}>
                    {joinedClasses.map(({ id, className, subject }) => (
                        <Grid item key={id} xs={12} sm={6} md={4}>
                            <Card sx={{ cursor: 'pointer' }}>
                                <CardContent>
                                    <Typography variant="h6">{className}</Typography>
                                    <Typography variant="body2">{subject}</Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>

                <Dialog open={open} onClose={handleClose}>
                    <DialogTitle>Join a Class</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Please enter the class code and your name.
                        </DialogContentText>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Class Code"
                            type="text"
                            fullWidth
                            value={classCode}
                            onChange={(e) => setClassCode(e.target.value)}
                        />
                        <TextField
                            margin="dense"
                            label="Your Name"
                            type="text"
                            fullWidth
                            value={studentName}
                            onChange={(e) => setStudentName(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">Cancel</Button>
                        <Button onClick={handleJoin} color="primary">Join</Button>
                    </DialogActions>
                </Dialog>

                <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={handleSnackbarClose}>
                    <Alert onClose={handleSnackbarClose} severity={snackbarSeverity} sx={{ width: '100%' }}>
                        {snackbarMessage}
                    </Alert>
                </Snackbar>
            </Box>
        </Container>
    );
}

export default Student;
