import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
    Typography, Card, CardContent, CardActions, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle,
    TextField, Grid, Box, IconButton, Container, CssBaseline, Avatar, Snackbar, Alert
} from '@mui/material';
import { ref, push, remove, onValue, query, orderByChild, equalTo } from 'firebase/database';
import DeleteIcon from '@mui/icons-material/Delete';
import ClassIcon from '@mui/icons-material/Class';
import { db } from './firebasec';

function Teacher() {
    const [open, setOpen] = useState(false);
    const [className, setClassName] = useState('');
    const [subject, setSubject] = useState('');
    const [classes, setClasses] = useState([]);
    const [error, setError] = useState('');
    const [email, setEmail] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const userEmail = localStorage.getItem('userEmail');
        if (userEmail) {
            setEmail(userEmail);
            fetchClasses(userEmail);
        } else {
            // Redirect to login page if no userEmail in local storage
            window.location.href = '/login';
        }
    }, []);

    const fetchClasses = (userEmail) => {
        const classesRef = query(ref(db, 'classes'), orderByChild('email'), equalTo(userEmail));
        onValue(classesRef, (snapshot) => {
            const data = snapshot.val();
            if (data) {
                const parsedClasses = Object.keys(data).map(key => ({
                    id: key,
                    ...data[key]
                }));
                setClasses(parsedClasses);
            } else {
                setClasses([]);
            }
        });
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleCreate = async (e) => {
        e.preventDefault();

        if (!className || !subject) {
            setError('Class name and subject are required');
            return;
        }

        const generateCode = () => {
            return Math.floor(10000 + Math.random() * 90000).toString();
        };

        const classCode = generateCode();

        try {
            const classRef = ref(db, 'classes');
            await push(classRef, {
                className,
                subject,
                email,
                code: classCode
            });
            setClassName('');
            setSubject('');
            setOpen(false);
            setError('');
        } catch (error) {
            console.error("Error creating class:", error.message);
            setError(`An error occurred while creating the class: ${error.message}`);
        }
    };

    const handleDelete = (e, id) => {
        e.stopPropagation();
        const classRef = ref(db, `classes/${id}`);
        remove(classRef).then(() => {
            setClasses(prevClasses => prevClasses.filter(cls => cls.id !== id));
        }).catch(error => {
            console.error("Error deleting class:", error.message);
        });
    };

    const handleCardClick = (id) => {
        navigate(`/classdetails/${id}`);
    };

    const handleCodeClick = (code) => {
        navigator.clipboard.writeText(code).then(() => {
            setSnackbarOpen(true);
        }).catch(err => {
            console.error('Failed to copy the text: ', err);
        });
    };

    const handleSnackbarClose = () => {
        setSnackbarOpen(false);
    };

    return (
        <Container component="main" maxWidth="md">
            <CssBaseline />
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <ClassIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Teacher Page
                </Typography>
                <Card sx={{ mt: 3, p: 3, width: '100%' }}>
                    <CardContent>
                        <Typography variant="h6">Manage Classes</Typography>
                    </CardContent>
                    <CardActions>
                        <Button variant="contained" color="primary" onClick={handleClickOpen}>Create Class</Button>
                    </CardActions>
                </Card>

                <Grid container spacing={2} sx={{ mt: 3 }}>
                    {classes.map(({ id, className, subject, code }) => (
                        <Grid item key={id} xs={12} sm={6} md={4}>
                            <Card onClick={() => handleCardClick(id)} sx={{ cursor: 'pointer' }}>
                                <CardContent>
                                    <Typography variant="h6">{className}</Typography>
                                    <Typography variant="body2">{subject}</Typography>
                                    <Typography variant="body2" onClick={(e) => { e.stopPropagation(); handleCodeClick(code); }} sx={{ cursor: 'pointer', color: 'blue', textDecoration: 'underline' }}>
                                        Code: {code}
                                    </Typography>
                                    <Typography variant="body2">
                                        Students:
                                        {Object.entries(classes.find(c => c.id === id).students || {}).map(([key, student]) => (
                                            <Typography key={key} variant="body2">{student}</Typography>
                                        ))}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <IconButton onClick={(e) => handleDelete(e, id)} color="secondary">
                                        <DeleteIcon />
                                    </IconButton>
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                </Grid>

                <Dialog open={open} onClose={handleClose}>
                    <DialogTitle>Create a New Class</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Please enter the class name and subject.
                        </DialogContentText>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Class Name"
                            type="text"
                            fullWidth
                            value={className}
                            onChange={(e) => setClassName(e.target.value)}
                        />
                        <TextField
                            margin="dense"
                            label="Subject"
                            type="text"
                            fullWidth
                            value={subject}
                            onChange={(e) => setSubject(e.target.value)}
                        />
                        {error && (
                            <Typography color="error" variant="body2">
                                {error}
                            </Typography>
                        )}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">Cancel</Button>
                        <Button onClick={handleCreate} color="primary">Create</Button>
                    </DialogActions>
                </Dialog>

                <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={handleSnackbarClose}>
                    <Alert onClose={handleSnackbarClose} severity="success" sx={{ width: '100%' }}>
                        Class code copied to clipboard!
                    </Alert>
                </Snackbar>
            </Box>
        </Container>
    );
}

export default Teacher;
